/* *
 * 投稿页面
 */
import { mapState, mapMutations, mapActions } from 'vuex';
import util, { parGetData, parSetData, checklogin } from '../../common/util';
export default {
	data() {
		return {
			text: '',
			image: '',
			imageurl: '',
			video: '',
			openid: '',
			userinfo: '',
			token: '',
			showlogin: false
		};
	},
	onShow() {
		this.checkstat();
		if (!this.token) {
			this.showlogin = true;
		}
	},
	onLoad() {
		this.checkstat();
		if (!this.token) {
			this.showlogin = true;
		}
	},
	methods: {
		checkstat() {
			var logininfo = util.checklogin();
			this.openid = logininfo.openid;
			this.userinfo = logininfo.userinfo;
			this.token = logininfo.token;
		},
		close: function(type) {
			this[type] = '';
		},
		chooseImage: function() {
			var self = this;
			if (self.image) {
				uni.showToast({
					title: '只可上传一张图片,将自动替换为新选择的',
					icon: 'none'
				});
			}
			uni.chooseImage({
				count: 1,
				sizeType: 'compressed ',
				success: res => {
					self.image = res.tempFilePaths[0];
					uni.uploadFile({
						url: util.apiurl + 'syj/index/upimg',
						filePath: res.tempFilePaths[0],
						name: 'image',
						header: {
							auth: util.httpauth
						},
						success: res => {
							res.data = JSON.parse(res.data);
							if (res.statusCode == 200 && res.data.code < 1) {
								self.imageurl = res.data.imageurl;
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								});
								self.image = self.imageurl = '';
							}
						}
					});
				}
			});
		},
		send: function(e) {
			if (!this.token) {
				this.showlogin = true;
				return false;
			}
			if (!this.text && !this.image && !this.video) {
				uni.showToast({
					title: '文字、图片、视频三者至少填写一个',
					icon: 'none'
				});
				return false;
			}
			uni.showLoading({
				title: '发布中请稍等...'
			});
			var self = this;
			if (self.video) {
				uni.uploadFile({
					url: util.apiurl + 'syj/index/tougao',
					filePath: self.video,
					name: 'video',
					formData: {
						text: self.text,
						image: self.imageurl
					},
					header: {
						auth: util.httpauth
					},
					success: res => {
						if ('string' === typeof res.data) {
							res.data = JSON.parse(res.data);
						}
						if (res.statusCode == 200 && res.data.code < 1) {
							uni.showToast({
								title: res.data.msg
							});
							setTimeout(function() {
								uni.hideLoading();
							}, 1000);
							self.text = self.image = self.video = '';
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: 'none'
							});
							setTimeout(function() {
								uni.hideLoading();
							}, 1000);
						}
					},
					fail: function() {
						uni.hideLoading();
					}
				});
			} else {
				uni.request({
					url: util.apiurl + 'syj/index/tougao',
					method: 'POST',
					data: {
						text: self.text,
						image: self.imageurl,
						token: self.token,
						shebei: util.shebei
					},
					success: ret => {
						if (ret.statusCode == 200 && ret.data.code < 1) {
							self.text = self.image = self.video = '';
							uni.showToast({
								title: ret.data.msg
							});
							setTimeout(function() {
								uni.hideLoading();
							}, 1000);
						} else {
							uni.showToast({
								title: ret.data.msg,
								icon: 'none'
							});
							setTimeout(function() {
								uni.hideLoading();
							}, 1000);
						}
					},
					fail: function() {
						uni.hideLoading();
					},
					header: {
						auth: util.httpauth
					}
				});
			}
		},
		chooseVideo: function() {
			var self = this;
			uni.chooseVideo({
				sourceType: ['album', 'camera'],
				success: res => {
					self.video = res.tempFilePath;
				}
			});
		},
		/* 登录方法 */
		login(res) {
			util.logincomm(res, this);
		}
	}
};
